import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import { object, string } from "yup";
import CustomFormField from "elements/CustomForm/CustomFormField";
import CustomFormSubmitBtn from "elements/CustomForm/CustomFormSubmitBtn";
import Dashboard from "..";

import { addTeacher } from "actions/project/addTeacher";
import Alert from "elements/Alert";

const AddTeacherSchema = object().shape({
  firstname: string()
    .min(3, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  lastname: string()
    .min(3, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  email: string().email("Invalid email").required("Required"),
  password: string()
    .min(6, "Too Short!")
    .max(20, "Too Long!")
    .required("Required"),
  subjects: string().required("Required"),
  role: string().oneOf(["teacher"]).required("Required"),
});

class AddTeacher extends PureComponent {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(values, { setSubmitting }) {
    // setTimeout(() => {
    //   alert(JSON.stringify(values, null, 2));
    //   setSubmitting(false);
    // }, 400);

    let v = { ...values };

    v.subjects = v.subjects.split(",").map((v) => v.trim());

    this.props.addTeacher(this.props.projectId, v, { setSubmitting });
  }

  render() {
    return (
      <Dashboard>
        <div className="AddTeacher w-1/2 mx-auto p-4 shadow-md bg-white">
          {this.props.addTeacherDetails.error &&
            this.props.addTeacherDetails.error.message && (
              // <p>{this.props.addTeacherDetails.error.message}</p>
              <Alert
                type="error"
                heading={this.props.addTeacherDetails.error.message}
              />
            )}

          {this.props.addTeacherDetails.data &&
            this.props.addTeacherDetails.data.success && (
              // <p>Successfully added teacher</p>
              <Alert type="success" heading="Successfully added teacher" />
            )}

          <Formik
            initialValues={{
              firstname: "",
              lastname: "",
              email: "",
              password: "",
              subjects: "",
              role: "teacher",
            }}
            validationSchema={AddTeacherSchema}
            onSubmit={this.handleSubmit}
          >
            {({ isSubmitting, isValid }) => (
              <Form>
                <CustomFormField
                  label="First Name"
                  name="firstname"
                  type="text"
                  placeholder="John"
                />
                <CustomFormField
                  label="Last Name"
                  name="lastname"
                  type="text"
                  placeholder="Wick"
                />
                <CustomFormField
                  label="Email"
                  name="email"
                  type="email"
                  placeholder="john@wick.com"
                />
                <CustomFormField
                  label="Password"
                  name="password"
                  type="password"
                  placeholder="Create teacher password"
                />
                <CustomFormField
                  label="Subjects"
                  name="subjects"
                  type="text"
                  placeholder="Subject1, Subject 2..."
                />

                <CustomFormField name="role" type="text" hidden={true} />
                <CustomFormSubmitBtn
                  isSubmitting={isSubmitting}
                  isValid={isValid}
                  btnText="Update Profile"
                  btnColorClass="bg-primary-2"
                  btnWidthClass="w-1/2"
                />
              </Form>
            )}
          </Formik>
        </div>
      </Dashboard>
    );
  }
}

const mapStateToProps = (state) => ({
  projectId:
    state.project.currentProject.data && state.project.currentProject.data._id,
  addTeacherDetails: state.project.teachers.add,
});

const mapDispatchToProps = { addTeacher };

export default connect(mapStateToProps, mapDispatchToProps)(AddTeacher);
