import { GET_PROJECT_TEACHERS } from "./types";

// Service
import getProjectTeachersService from "services/api/project/getProjectTeachers.service";

export const getProjectTeachers = (projectId) => async (dispatch) => {
  try {
    dispatch({ type: GET_PROJECT_TEACHERS.TRIGGER });

    dispatch({ type: GET_PROJECT_TEACHERS.REQUEST });

    // console.log("projectID = ", projectId);

    let res = await getProjectTeachersService(projectId);

    dispatch({
      type: GET_PROJECT_TEACHERS.SUCCESS,
      payload: res,
    });
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: GET_PROJECT_TEACHERS.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: GET_PROJECT_TEACHERS.FULFIL });
  }
};
