import axios from "utils/axiosInstance.util";
import { getUserFromLocalStorage } from "utils/auth.util";

export default async (projectId, value) => {
  let token = getUserFromLocalStorage();

  let url = `/api/${process.env.REACT_APP_API_VERSION}/project/${projectId}/add/teacher`;

  let { data } = await axios.post(url, value, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });

  return data;
};
