import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import { connect } from "react-redux";

// Actions
import { getProjectTeachers } from "actions/project/getProjectTeachers";

const data = {
  headings: [
    {
      key: "teacher",
      value: "Teacher",
    },
    {
      key: "email",
      value: "Email",
    },
  ],
};

const TeachersList = ({ projectTeachers, getProjectTeachers }) => {
  let { projectId } = useParams();

  useEffect(() => {
    getProjectTeachers(projectId);
  }, []);

  return (
    <div className="TeachersList">
      <div className="overflow-x-auto bg-white rounded-lg shadow overflow-y-auto relative h-auto">
        <table className="border-collapse table-auto w-full whitespace-no-wrap bg-white table-striped relative">
          <thead>
            <tr className="text-left">
              {data.headings.map((heading) => (
                <th
                  key={heading.key}
                  className="bg-gray-100 sticky top-0 border-b border-gray-200 px-6 py-2 text-gray-600 font-bold tracking-wider uppercase text-xs"
                >
                  {heading.value}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {projectTeachers.data &&
              projectTeachers.data.map((user) => (
                <tr key={user.id}>
                  <td className="border-dashed border-t border-gray-200 firstName">
                    <span className="text-gray-700 px-6 py-3 flex items-center">
                      {user.fullname}
                    </span>
                  </td>
                  <td className="border-dashed border-t border-gray-200 gender">
                    <span className="text-gray-700 px-6 py-3 flex items-center">
                      {user.email}
                    </span>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  projectTeachers: state.project.teachers.list,
});

const mapDispatchToProps = { getProjectTeachers };

export default connect(mapStateToProps, mapDispatchToProps)(TeachersList);
