import { ADD_TEACHER } from "./types";

// Service
import addTeacherService from "services/api/project/addTeacher.service";
import history from "../../history";

export const addTeacher = (projectId, value, { setSubmitting }) => async (
  dispatch
) => {
  try {
    dispatch({ type: ADD_TEACHER.TRIGGER });

    dispatch({ type: ADD_TEACHER.REQUEST });

    let res = await addTeacherService(projectId, value);

    dispatch({
      type: ADD_TEACHER.SUCCESS,
      payload: res,
    });

    history.push(`/project/${projectId}/teachers`);
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: ADD_TEACHER.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: ADD_TEACHER.FULFIL });
    setSubmitting(false);
  }
};
